//顏色
$proColor :rgb(68, 157, 83);
$secColor :rgb(255, 238, 168);
$thirdColor :rgb(251, 240, 194);

//文字
$titleSize:2em;
$subTitlteSize:1.5em;
$pContextSize:1.2em;
$titleColor:rgb(3, 21, 6);

.blogJ {
    font-family:Avenir;
    font-size:14px;
    .head {
        text-align: left;
        margin-bottom: 1.5em;
        .subTitle {
            font-weight: normal;
            text-transform: uppercase;
            font-size: 0.8em;
            letter-spacing: -1px;
        }
        .title {
           font-size: 2.5em; 
           margin: 0.8em 0;
           font-family: Arial;
        }
        .subDesc {
            ul {
                display: flex;
                justify-content: space-around;
                width: 33%;
                font-size: 0.8em; 
            }
        }
        
    }
    .content {
        .contant {
            p {
                text-align: left;
                text-indent: 2em;
            }
            .tableWrap{
                background: linear-gradient(217deg, rgba(68, 157, 83, 0.8), rgba(255,0,0,0) 70.71%),
                                linear-gradient(127deg, rgba(242, 198, 20, 0.8), rgba(0,255,0,0) 70.71%),
                                linear-gradient(336deg, rgba(26, 62, 86, 0.8), rgba(0,0,255,0) 70.71%);
                padding: 2em 1.5em ;
                .tableTitle {
                    h1{
                        font-weight: 900;
                        font-size: 2em;
                        margin-bottom: 0.4em;
                        color: rgb(68, 157, 83);
                        //text-shadow: 5px 5px 10px #000;
                    }
                    @supports (-webkit-text-stroke-width: 2px){
                        h1::after {
                            //-webkit-text-stroke-width: 2px;
                            //-webkit-text-stroke-color: $secColor;
                        }
                    }
                }
                .tableContnet {
                    border-collapse: collapse;
                    td,th{
                        padding: 0.2em 0.5em;
                    }
                    thead{
                        color: $thirdColor;
                        background: $proColor;
                    }
                    tbody {
                        color: rgb(3, 35, 9);
                        tr{
                            td:first-child {
                                width: 20%;
                            }
                            td:last-child {
                                width: 50%;
                                text-align: left;
                            }
                            
                        }
                        tr:nth-child(even){
                            background: $secColor;
                        }
                        tr:nth-child(odd){
                            background-color:rgb(255, 255, 255);
                        }
                    }
                    tfoot {
                        tr{
                            td {
                                text-align: right;
                                color: $thirdColor;
                            }
                        }
                    }
                }
            }
        }
    }
}
