.comfortableIMG{
    background-size: contain;
}
.infoContent {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.saleTreeBox {
    background-image: url(/src/images/saleTree/bg-onlyleaf.jpg);
    background-repeat: no-repeat;
    background-position: bottom left;
    background-size: contain;
    background-color: #ebebeb;
    padding: unset !important;
    //padding-top: 4em !important;
    .ant-menu,.ant-pagination,.ant-select,.ant-select-selector,.ant-table,.ant-layout-sider,.ant-input-affix-wrapper,.searchInput >.ant-input {
        background: transparent !important;
    }
    .ant-form-item {
        margin-bottom: unset !important;
    }
    .ant-input-affix-wrapper {
        border: none;
    }
    .hero-header {
        background-image: url(/src/images/saleTree/heaaderBG.jpg);
        background-repeat: no-repeat;
        background-position: bottom 30% right;
        background-size: cover;
        height: 300px;
    }
    .contentHeader {
        display: flex;
        justify-content: space-between;
        .R-control {
            flex: 0 1 20em;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-around;
            align-items: center;
            .ant-select-selector {
                border: none;
            }
            .ant-select {
                text-align: left;
                width: 12em;
            }
        }
    }

    
    .contentBox {
        padding: 2em;
    }
    
}
img.ant-image-preview-img {
    object-fit: contain ;
}