.nav-container {
    background-color: transparent;
    transition: all 0.3s ease-in-out;
    position:fixed;
    top: 0;
    width: 100%;
    z-index:  1000;
    
    .ant-menu-light,.ant-menu-item-selected {
        color:#fff;
    }
    ul {
        display: flex;
        justify-content: flex-end;
        //margin-right: 3rem;
        width: 100%;
        li {
            padding: 0.5rem;
            a {
                //font-size: 1.5rem;
                color: #fff;
            }
        }
    }
}

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5em 2em 0em 2em;
    
    .logo {
        color: rgb(197, 60, 60);
        font-weight: bold;
        img{
            width: 80px;
        }
    }

    ul:not(.ant-pagination){
        //display: flex;
        //width: 100%;
        overflow: hidden;
        a {
            //display: block;
            padding: 0 1em;
        }
    }

}
.social-header {
    display: none;
}

@media only screen and (min-width:1050px){
    .hero {
        height: 90vh;
        background-image: linear-gradient(to right,rgba(110, 175, 112,1) 66%,rgba(255, 255, 255, 1));
    }

    .nav-container {
        display: grid;
        grid-template-columns: 66% auto;
        //background: unset;
        //background-image: linear-gradient(to right,rgb(92, 146, 93) 66%,rgba(255, 255, 255, 1));
        background-color: transparent;
        //box-shadow: 0px 5px 5px rgb(74, 105, 97);
    }
    header {
        padding: 0.5em 2em 0em 4em;
    }
    .social-header {
        padding: 1em 4em 1m 0;
    }


    .social-header {
        display: block;
        margin: auto 0px;

        ul {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 5em;
            float: right;

            img {
                width: 20px;
            }
        }
    }
}

@media only screen and (min-width:1250px){
    header {
        padding: 0.5em 0em 0em 8em;
    }
    .social-header {
        padding: 1em 8em 1em 0;
    }
}

@media only screen and (min-width:1550px){
    header {
        padding: 0.5em 2em 0em 18em;
        
    }
    .social-header {
        padding: 1em 18em 1em 0;
    }
}